@use 'base/variables' as v;
@use 'base/mixins' as m;

.features {
    margin-top: 15rem;

    @include m.desktop {
        margin-top: 20rem;
    }
    
    &__heading {
        @include m.heading;
    }

    &__subheading {
        @include m.subheading;
    }

    &__content {
        margin-top: 3rem;
        background-repeat: no-repeat;
        background-position: center 10%;
        background-size: 31rem;

        @include m.tablet {
            padding: 7rem 0;
            background-size: 38rem;
        }

        @include m.desktop {
            padding: 12rem 0;
            background-position: -5rem center;
            background-size: 53%;
        }

        @include m.desktopxl {
            margin-top: 10rem;
        }

        @include m.ultra {
            background-position: 32% center;
            background-size: 80rem;
        }
    }
}

.notwebp.notavif .features__content {
    background-image: url(../img/image-computer.png);
}

.webp .features__content {
    background-image: url(../img/image-computer.webp);
}

.avif .features__content {
    background-image: url(../img/image-computer.avif);
}

.features {
    &__grid {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: repeat(3, 1fr);
        gap: 30rem;

        @include m.desktop {
            grid-template-columns: repeat(2, 1fr);
            grid-template-rows: unset;
            gap: 20%;
        }
    }

    &__list {
        grid-row: 2 / 4;

        @include m.desktop {
            grid-row: unset;
            grid-column: 2 / 3;
        }
    }
}

.feature {
    margin-bottom: 5rem;
    text-align: center;

    &:last-of-type {
        margin-bottom: 0;
    }

    @include m.desktop {
        margin-bottom: 7rem;
    }

    &:last-of-type {
        margin-bottom: 0;
    }

    &__name {
        font-size: 2.3rem;
        font-weight: 600;
        color: v.$darkGray;

        @include m.desktop {
            text-align: left;
            font-size: 3rem;
        }
    }

    &__description {
        margin-top: 1rem;
        line-height: 1.6;
        color: v.$lightGray;

        @include m.desktop {
            text-align: left;
            font-size: 2rem;
            line-height: 2; 
        }
    }
}