@use 'base/variables' as v;
@use 'base/mixins' as m;

.workflow {
   margin-top: 10rem;
   
    &__heading {
        @include m.heading;
    }
    
    &__subheading {
        @include m.subheading;
        margin-bottom: 9rem;
    }

    &__grid {
        @include m.desktop {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            gap: 3rem; 
        }
    }
}

.icon {
    margin-bottom: 5rem;
    text-align: center;

    @include m.desktop {
        margin-bottom: 0;
    }

    &__box {
        margin-bottom: 3.8rem;
        height: 4rem;
        display: flex;
        justify-content: center;
        align-items: center;

        @include m.desktop {
            margin-bottom: 6rem;
            height: 6rem; 
        }
    }
    
    &__image {
        width: 4.3rem;
           
        @include m.desktop {
            width: 5.5rem;
        }
    }

    &__name {
        margin-bottom: 1.8rem;
        font-size: 2.3rem;
        font-weight: 600;
        color: v.$darkGray;

        @include m.desktop {
            font-size: 3rem;
        }
    }

    &__text {
        margin-top: 0;
        line-height: 1.8;
        color: v.$lightGray;

        @include m.desktop {
            font-size: 2.1rem;
        }
    }
}