@use 'base/variables' as v;
@use 'base/mixins' as m;

.clients {
    padding: 8rem 0;

    @include m.desktop {
        padding: 19rem 0;
    }
    
    &__grid {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 3rem;
        
        @include m.desktop {
            flex-direction: row;
            justify-content: space-between;
        }
    }

    &__logo {
        transform: scale(.65);

        @include m.desktop {
            transform: scale(1);
        }
    }
}