@use 'base/variables' as v;
@use 'base/mixins' as m;

.platforms {
    &__heading {
        @include m.heading;
    }

    &__subheading {
        @include m.subheading;
        margin-bottom: 6rem;
    }
    
    &__grid {
        display: flex;
        flex-direction: column;
        gap: 2rem;
        text-align: center;

        @include m.tablet {
            flex-direction: row;
            justify-content: center;
        }
    }

    &__btn {
        @include m.btn(v.$primary);
    }

    &__btn--blue {
        @include m.btn(v.$secondary);
    }
}