  transition-duration: 0.3s;
}
.footer__link:hover {
  color: #26baa4;
}
@media (min-width: 768px) {
  .footer__link {
    font-size: 2.1rem;
  }
}
@media (min-width: 1200px) {
  .footer__link {
    font-size: 2.4rem;
  }
}
@media (min-width: 768px) {
  .footer__link:nth-child(2) {
    order: 3;
  }
}
@media (min-width: 768px) {
  .footer__link:nth-child(3) {
    order: 1;
  }
}
@media (min-width: 768px) {
  .footer__link:nth-child(4) {
