.social {
  margin-top: 6rem;
  height: 2.5rem;
  display: flex;
  gap: 4rem;
}
@media (min-width: 992px) {
  .social {
    margin: 0;
  }
}
@media (min-width: 1200px) {
  .social {
    height: 3.2rem;
  }
}
.social__link::before {
  content: "";
  display: block;
  background-repeat: no-repeat;
  height: 2.5rem;
  width: 2.5rem;
  background-size: 2.5rem;
}
@media (min-width: 1200px) {
  .social__link::before {
    height: 3.2rem;
    width: 3.2rem;
    background-size: 3.2rem;
  }
}
.social__link[href*="facebook.com"]::before {
  background-image: url(../img/icon-facebook.svg);
}
.social__link[href*="twitter.com"]::before {
  background-image: url(../img/icon-twitter.svg);
}
.social__link[href*="instagram.com"]::before {
  background-image: url(../img/icon-instagram.svg);
  margin-right: 0;
}
.social__link {
  transition-property: filter;
  transition-duration: 0.3s;
}
.social__link:hover {
  filter: brightness(0) saturate(100%) invert(64%) sepia(11%) saturate(2617%) hue-rotate(122deg) brightness(95%) contrast(82%);
}
.social__accessible {
  display: block;
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0;
  margin: 0;
  overflow: hidden;
}

