@use 'variables' as v;

/** Media Queries **/
@mixin phone {
    @media (min-width: v.$phone) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}

@mixin desktopxl {
    @media (min-width: v.$desktopxl) {
        @content;
    }
}

@mixin desktopxxl {
    @media (min-width: v.$desktopxxl) {
        @content;
    }
}

@mixin ultra {
    @media (min-width: v.$ultra) {
        @content;
    }
}

