@use 'base/variables' as v;
@use 'base/mixins' as m;

.cloud {
    margin-top: 17rem;

    @include m.tablet {
        margin-top: 10rem;
    }
    
    @include m.desktopxl {
        margin-top: 20rem;
    }

    &__heading {
        @include m.heading;
    }

    &__subheading {
        @include m.subheading;
    }

    &__image-box {
        display: block;
        margin-top: 5rem;

        @include m.tablet {
            margin-top: 13rem;
        }
    }

    &__image {
        width: 100%;
    }
}