@use 'variables' as v; 

html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
  
body {
    font-family: v.$mainFont;
    font-size: 1.6rem;
    font-weight: 300;
}

.container {
    width: 83%;
    max-width: 1200px;
    margin: 0 auto;
}

.container-xl {
    width: 83%;
    max-width: 1440px;
    margin: 0 auto;
}

img {
    max-width: 100%;
    display: block;
}

ul {
    padding:0;
    list-style: none;
    margin: 0;
}