@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    margin-top: 20rem;
    padding: 6rem 0;
    background-color: lighten(v.$lightGray, 30%);

    &__grid {
        display: flex;
        flex-direction: column;
        align-items: center;
        
        @include m.desktop {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
        }
    }

    &__content {

        @include m.tablet {
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 6rem;
        }

        @include m.desktopxl {
            gap: 11rem;
        }

        @include m.desktopxxl {
            gap: 17rem;
        }
    }

    &__logo {
        margin: 0 auto 4rem auto;
        width: 5.5rem;

        @include m.tablet {
            margin: 0;
        }

        @include m.desktop {
            width: 7rem;
        }
    }

    &__nav {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 3rem;

        @include m.tablet {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
        }

        @include m.desktop {
            margin: 0 6rem 0 0;
        }

        @include m.desktopxxl {
            margin-right: 10rem;
            gap: 2.5rem 8rem;
        }
    }


    &__link {
        font-size: 1.8rem;
        color: v.$darkGray;
        transition-property: color;
        transition-duration: .3s;

        &:hover {
            color: v.$primary;
        }

        @include m.tablet {
            font-size: 2.1rem;
        }

        @include m.desktopxl {
            font-size: 2.4rem;
        }

        &:nth-child(2) {
            @include m.tablet {
                order: 3;
            }
        }
        &:nth-child(3) {
            @include m.tablet {
                order: 1;
            }
        }
        &:nth-child(4) {
            @include m.tablet {
                order: 4;
            }
        }
        &:nth-child(5) {
            @include m.tablet {
                order: 2;
            }
        }
    }
}

.social {
    margin-top: 6rem;
    height: 2.5rem;
    display: flex;
    gap: 4rem;

    @include m.desktop {
        margin: 0;
    }
    
    @include m.desktopxl {
        height: 3.2rem;
    }

    &__link::before {
        content: '';
        display: block;
        background-repeat: no-repeat;
        height: 2.5rem;
        width: 2.5rem;
        background-size: 2.5rem;

        @include m.desktopxl {
            height: 3.2rem;
            width: 3.2rem;
            background-size: 3.2rem;
        }
    }
    
    &__link[href*="facebook.com"]::before {
        background-image: url(../img/icon-facebook.svg);
    }
    &__link[href*="twitter.com"]::before {
        background-image: url(../img/icon-twitter.svg);
    }
    &__link[href*="instagram.com"]::before {
        background-image: url(../img/icon-instagram.svg);
        margin-right: 0;
    }

    &__link {
        transition-property: filter;
        transition-duration: .3s;
        
        &:hover {
            filter: brightness(0) saturate(100%) invert(64%) sepia(11%) saturate(2617%) hue-rotate(122deg) brightness(95%) contrast(82%);
        }
    }

    &__accessible { 
        display: block;
        width: 1px;
        height: 1px;
        padding: 0;
        border: 0;
        margin: 0;
        overflow: hidden;
    }
}

.attribution { font-size: 11px; text-align: center; }
.attribution a { color: hsl(228, 45%, 44%); }