@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
    padding-top: 12rem;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: bottom;

    @include m.tablet {
        padding-top: 17rem;
        background-size: contain;
        background-position: top;
    }

    @include m.ultra {
        background-size: cover;
    }
}

.notavif.notwebp .header {
    background-image: url(../img/bg-header-mobile.png);

    @include m.tablet {
        background-image: url(../img/bg-header-desktop.png);
    }
}

.webp .header {
    background-image: url(../img/bg-header-mobile.webp);

    @include m.tablet {
        background-image: url(../img/bg-header-desktop.webp);
    }
}

.avif .header {
    background-image: url(../img/bg-header-mobile.avif);

    @include m.tablet {
        background-image: url(../img/bg-header-desktop.avif);
    }
}

.header {
    &__brand {
        display: flex;
        justify-content: center;
    }

    &__brand-image {
        @include m.desktop {
            width: 16rem;
        }
    }

    &__content {
        max-width: 90rem;
        margin: 0 auto;
        text-align: center;
        animation: 1s ease-in-out 0s 1 mostrarImagen;
    }

    &__heading {
        margin-top: 6.5rem;
        font-size: 3.2rem;
        font-weight: 600;
        color: v.$darkGray;

        @include m.tablet {
            font-size: 4.5rem;
        }

        @include m.desktop {
            margin-top: 7.5rem;
            font-size: 5.8rem;
        }
    }

    &__intro {
        margin-bottom: 4.5rem;
        color: v.$lightGray;
        line-height: 1.5;

        @include m.phone {
            font-size: 2.1rem;
        }

        @include m.desktop {
            margin-bottom: 6rem;
            font-size: 2.6rem;
        }
    }

    &__grid {
        display: flex;
        flex-direction: column;
        gap: 2rem;

        @include m.phone {
            flex-direction: row;
            justify-content: center;
        }
    }

    &__btn {
        @include m.btn(v.$primary);
    }

    &__btn--blue {
        @include m.btn(v.$secondary);
    }
}

@keyframes mostrarImagen {
    0% {
        opacity: 0;
        transform: translateY(7rem);
    }
    50% {
        opacity: 0;
    }
    100% {
        opacity: 1;
        transform: translateY(0rem);
    }
}